:root {
  --color-dark-text: #494450;
  --color-text: #615c69;
  --color-light-text: #d2bea6;
  --font-bebas: 'Bebas Neue', Arial, Helvetica, sans-serif;
  --bg-body: #f1e2cf;
  --bg-light: #e8d7c3;
  --bg-dark: #494450;
  --offset-page: 30px;
  --transition: 0.2s ease-in-out;
}

@font-face {
  font-family: 'Bebas Neue';
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/BebasNeueBold.woff2) format('woff2'), url(../fonts/BebasNeueBold.woff) format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-Black.woff2) format('woff2'), url(../fonts/Montserrat-Black.woff) format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-Medium.woff2) format('woff2'), url(../fonts/Montserrat-Medium.woff) format('woff');
}

html {
  min-width: 320px;
  height: 100%;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  color: var(--color-text);
  background-color: var(--bg-body);
}

abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  border-bottom: none;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

img {
  max-width: 100%;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-size: 100%;
  line-height: 1.15;
  font-family: inherit;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  display: table;
  box-sizing: border-box;
  max-width: 100%;
  padding: 0;
  color: inherit;
  white-space: normal;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: textfield;
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

details {
  display: block;
}

summary {
  display: list-item;
}

del::before,
del::after {
  content: '\00a0';
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}

body > div.page-grid {
  flex-grow: 1;
  padding-top: 82px;
}

.col {
  box-sizing: border-box;
  min-width: 320px;
  max-width: 1620px;
  margin-right: auto;
  margin-left: auto;
  padding-right: var(--offset-page);
  padding-left: var(--offset-page);
}

.page-content {
  width: 100%;
  max-width: 1100px;
}

.emoji {
  display: inline-block;
  vertical-align: text-bottom;
}

h2 {
  width: 100%;
  margin: 0.7em 0 0;
  font-size: 22px;
  line-height: 45px;
  font-family: var(--font-bebas);
  text-align: center;
  color: var(--color-light-text);
  text-transform: uppercase;
  letter-spacing: 2px;
}

.cource-wrap-level {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, 340px);
  margin-bottom: 30px;
  padding-bottom: 65px;
  justify-items: center;
  gap: 20px;
}

.cource-wrap-level h2 {
  grid-column-end: -1;
  grid-column-start: 1;
}

.btn {
  display: inline-block;
  box-sizing: border-box;
  padding: 13px 50px 13px;
  font-weight: 700;
  font-size: 22px;
  line-height: 1;
  font-family: var(--font-bebas);
  text-align: center;
  color: var(--color-light-text);
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2.5px;
  background-color: var(--bg-body);
  border: 0;
  border: 1px solid #d1bda5;
  border-radius: 26px;
  transition: box-shadow var(--transition);
  appearance: none;
}

.btn:hover {
  box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

.btn:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
  box-shadow: 0px 2px 4px 0px #3a2b4f;
}

.btn:active {
  box-shadow: 0px 2px 4px 0px transparent;
}

.btn--success {
  color: var(--color-light-text);
  background-color: var(--bg-dark);
  border: 1px solid var(--bg-dark);
  box-shadow: 0px 4px 16px 0px #3a2b4f;
  transition: box-shadow var(--transition);
}

.btn--success:hover {
  box-shadow: 0px 2px 4px 0px #3a2b4f;
}

.btn--success:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
}

.btn--success:active {
  box-shadow: 0px 2px 4px 0px transparent;
}

.btn--text-small {
  letter-spacing: 1px;
}

.btn:disabled {
  box-shadow: none;
  opacity: 0.6;
}

.btn::before {
  content: '';
  display: inline-block;
  height: 1em;
}

.page-header {
  position: relative;
  z-index: 2;
  color: var(--color-light-text);
}

.page-footer__col {
  padding-right: 30px;
  padding-left: 30px;
}

.page-header__top {
  position: relative;
  padding: 15px;
  text-align: center;
  background-color: var(--bg-dark);
}

.page-header__nav-toggle {
  width: 24px;
  height: 22px;
  padding: 0;
  background-color: transparent;
  background-image: url(../img/icon/page-header-toggle.svg);
  border: 0;
  box-shadow: none;
  transition: opacity var(--transition);
}

.page-header__nav-toggle:hover {
  opacity: 0.2;
}

.page-header__nav-toggle:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: 2px;
}

.page-header__logo:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: 2px;
}

.page-header__toggle {
  background-color: var(--bg-dark);
}

.page-header__user-wrap {
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: #55505d;
}

.page-header__user-wrap > div {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  min-width: 320px;
  max-width: 320px;
  padding: 24px 30px 24px 24px;
}

.page-header__user-name {
  display: flex;
  align-items: center;
  margin: 0;
  font-weight: 700;
  font-size: 20px;
  font-family: var(--font-bebas);
  color: var(--color-light-text);
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  transition: opacity var(--transition);
}

.page-header__user-name:hover {
  opacity: 0.7;
}

.page-header__user-name:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #7b7682;
  outline-offset: 2px;
}

.page-header__user-ava {
  display: block;
  width: 60px;
  height: 60px;
  margin-right: 10px;
  background-color: #686370;
  background-image: url(../img/ava_default.svg);
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
}

.page-header__user-exit {
  font-size: 20px;
  font-family: var(--font-bebas);
  color: #7b7682;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  border-bottom: 1px dashed #7b7682;
  transition: opacity var(--transition);
}

.page-header__user-exit:hover {
  opacity: 0.7;
}

.page-header__user-exit:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #7b7682;
  outline-offset: 2px;
}

.page-header__nav {
  box-sizing: border-box;
  min-width: 320px;
  padding: 23px var(--offset-page);
  border-bottom: 1px solid #504b58;
}

.page-header__nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.page-header__nav-item {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: baseline;
}

.page-header__nav-item svg {
  margin-right: 32px;
  transition: transform var(--transition), fill var(--transition);
  fill: var(--color-text);
}

.page-header__nav-item a {
  width: 100%;
  font-weight: 700;
  font-size: 26px;
  line-height: 28px;
  padding: 11px 0;
  font-family: var(--font-bebas);
  color: var(--color-light-text);
  text-decoration: none;
  letter-spacing: 2px;
}

.page-header__nav-item a:hover svg {
  transform: translateX(4px);
  fill: var(--color-light-text);
}

.page-header__nav-item a:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #7b7682;
  outline-offset: 4px;
}

.page-header__bottom {
  padding: 25px;
}

.page-header__bottom p {
  margin: 0 0 24px;
  font-size: 14px;
  line-height: 22px;
  color: #7b7682;
}

.page-header__bottom-btn {
  color: #7b7682;
  background-color: inherit;
  border-color: #7b7682;
}

.page-header__bottom-btn:hover {
  box-shadow: 0px 4px 20px 0px #7b7682;
}

.page-header__bottom-btn:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #7b7682;
  outline-offset: -1px;
  box-shadow: 0 0 0 0 transparent;
}

.page-header__bottom-btn:active {
  box-shadow: 0 0 0 0 transparent;
}

.cource-wrap-link {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 300px));
  justify-content: center;
  justify-items: center;
  gap: 20px;
  margin-bottom: 30px;
}

.cource-wrap-link h2 {
  grid-column-start: 1;
  grid-column-end: -1;
}

.cource-link {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  max-width: 300px;
  min-height: 80px;
  padding: 10px 60px;
  overflow: hidden;
  font-weight: 900;
  font-size: 20px;
  line-height: 22px;
  text-align: center;
  color: var(--color-dark-text);
  text-decoration: none;
  background: var(--bg-body);
  border-radius: 20px;
  box-shadow: 0 6px 18px var(--color-light-text);
  transition: background var(--transition);
}

.cource-link svg {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: auto;
  height: 100%;
  opacity: 0;
  transition: opacity var(--transition), fill var(--transition);
}

.cource-link:hover svg {
  opacity: 0.1;
  fill: var(--color-dark-text);
}

.cource-link:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
}

.cource-link:focus-visible svg {
  opacity: 0.05;
}

.cource-link:active,
.cource-link--active {
  color: var(--color-light-text);
  background-color: var(--bg-dark);
}

.cource-link:active svg,
.cource-link--active svg {
  opacity: 0.1;
  fill: var(--color-light-text) !important;
}

.card-program {
  width: 100%;
  max-width: 340px;
  border-radius: 20px;
  box-shadow: 0px 6px 18px 0px var(--color-light-text);
  font-size: 14px;
  line-height: 1.15;
}

.card-program__top {
  box-sizing: border-box;
  padding: 18px 16px 16px;
  text-align: center;
  background-color: var(--color-light-text);
  border-radius: 20px 20px 0 0;
}

.card-program__title {
  margin: 0 0 5px;
  font-weight: 900;
  font-size: 20px;
  line-height: 22px;
  color: var(--color-dark-text);
  text-transform: uppercase;
}

.card-program__bottom {
  position: relative;
  z-index: 2;
  box-sizing: border-box;
  margin-top: -16px;
  padding: 0 20px 10px;
  padding-top: 16px;
  text-align: center;
}

.card-program__bottom .btn {
  width: 100%;
  margin-bottom: 10px;
  font-size: 26px;
  letter-spacing: 2px;
}

.card-program__icon {
  position: relative;
  z-index: 2;
  margin-top: 5px;
  padding-bottom: 68.825%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.card-program__icon--buttocks-1 {
  background-image: url(../img/card-program/buttocks/hobbyist.png);
}

.card-program__icon--buttocks-professional-1 {
  background-image: url(../img/card-program/buttocks/professional-1-3-4-5.png);
}

.card-program__icon--buttocks-professional-2 {
  background-image: url(../img/card-program/buttocks/professional-2.png);
}

.card-program__icon--buttocks-professional-3 {
  background-image: url(../img/card-program/buttocks/professional-1-3-4-5.png);
}

.card-program__icon--buttocks-professional-4 {
  background-image: url(../img/card-program/buttocks/professional-1-3-4-5.png);
}

.card-program__icon--buttocks-professional-5 {
  background-image: url(../img/card-program/buttocks/professional-1-3-4-5.png);
}

.card-program__icon--weight-1 {
  background-image: url(../img/card-program/weight/hobbyist.png);
}

.card-program__icon--weight-professional-1 {
  background-image: url(../img/card-program/weight/professional.png);
}

.card-program__icon--weight-professional-2 {
  background-image: url(../img/card-program/weight/professional.png);
}

.card-program__icon--weight-professional-3 {
  background-image: url(../img/card-program/weight/professional.png);
}

.card-program__icon--weight-professional-4 {
  background-image: url(../img/card-program/weight/professional.png);
}

.card-program__icon--weight-professional-5 {
  background-image: url(../img/card-program/weight/professional.png);
}

.card-program__icon--body-1 {
  background-image: url(../img/card-program/body/hobbyist.png);
}

.card-program__icon--body-professional-1 {
  background-image: url(../img/card-program/body/professional-1.png);
}

.card-program__icon--body-professional-2 {
  background-image: url(../img/card-program/body/professional-2-3-4-5.png);
}

.card-program__icon--body-professional-3 {
  background-image: url(../img/card-program/body/professional-2-3-4-5.png);
}

.card-program__icon--body-professional-4 {
  background-image: url(../img/card-program/body/professional-2-3-4-5.png);
}

.card-program__icon--body-professional-5 {
  background-image: url(../img/card-program/body/professional-2-3-4-5.png);
}

.card-program__icon--back-1 {
  background-image: url(../img/card-program/back/hobbyist.png);
}

.card-program__icon--back-professional-1 {
  background-image: url(../img/card-program/back/professional-1.png);
}

.card-program__icon--back-professional-2 {
  background-image: url(../img/card-program/back/professional-2-3-4-5.png);
}

.card-program__icon--back-professional-3 {
  background-image: url(../img/card-program/back/professional-2-3-4-5.png);
}

.card-program__icon--back-professional-4 {
  background-image: url(../img/card-program/back/professional-2-3-4-5.png);
}

.card-program__icon--back-professional-5 {
  background-image: url(../img/card-program/back/professional-2-3-4-5.png);
}

.level-program {
  display: inline-grid;
  grid-template-columns: repeat(3, 24px);
  gap: 4px;
}

.level-program span {
  height: 10px;
  border: 1px solid #fff;
}

.level-program--1 span:first-child {
  background-image: linear-gradient(to right, #fff 50%, transparent 50%);
}

.level-program--2 span:first-child {
  background-color: #fff;
}

.level-program--3 span:first-child {
  background-color: #fff;
}

.level-program--3 span:nth-child(2) {
  background-image: linear-gradient(to right, #fff 50%, transparent 50%);
}

.level-program--4 span:first-child {
  background-color: #fff;
}

.level-program--4 span:nth-child(2) {
  background-color: #fff;
}

.level-program--5 span:first-child {
  background-color: #fff;
}

.level-program--5 span:nth-child(2) {
  background-color: #fff;
}

.level-program--5 span:last-child {
  background-image: linear-gradient(to right, #fff 50%, transparent 50%);
}

.level-program--6 span {
  background-color: #fff;
}

.program__header {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 68.75vw;
  min-height: 250px;
  margin-right: calc(var(--offset-page) * -1);
  margin-left: calc(var(--offset-page) * -1);
  background-image: linear-gradient(to top, #414141, transparent), url(../img/program/bg-header-buttocks.jpg);
  background-repeat: no-repeat;
  background-position: center bottom 60px;
  background-size: cover;
}

.program__heading {
  max-width: 77%;
  margin: 0 0 20px;
  font-weight: 900;
  font-size: 28px;
  line-height: 30px;
  font-family: var(--font-bebas);
  color: var(--bg-body);
  text-transform: uppercase;
  letter-spacing: 4px;
}

.program__heading span {
  display: inline-block;
  width: 100%;
  font-size: 15px;
}

.program__info {
  margin: 0 calc(var(--offset-page) * -1);
  padding-top: 14.5px;
  padding-bottom: 14px;
  color: var(--bg-body);
  background-color: var(--color-light-text);
}

.program__header-list {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.program__header-item {
  font-size: 10px;
  font-size: 14px;
}

.program__header-list-title {
  display: block;
  font-weight: 900;
  font-size: 14px;
  line-height: 20px;
  color: var(--color-dark-text);
}

.program__description-list-wrap {
  box-sizing: border-box;
  padding-right: 15px;
  border: 1px solid var(--color-light-text);
  border-radius: 7px;
}

.program__description-list-wrap h2 {
  color: var(--color-dark-text);
}

.program__description-list {
  padding: 0;
  padding-left: 24px;
  list-style-type: none;
}

.program__description-item {
  position: relative;
}

.program__description-item::before {
  content: '';
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 8px;
  background-color: var(--color-light-text);
  border-radius: 50%;
}

.inventory-wrap {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  padding-top: 16px;
  padding-bottom: 20px;
  background-color: var(--color-light-text);
}

.inventory-wrap h2 {
  align-self: flex-start;
  margin: 0;
  color: var(--color-dark-text);
}

.no-js .inventory-list {
  display: flex;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-snap-type: x mandatory;
}

.no-js .inventory-item {
  flex-shrink: 0;
  flex-basis: 33.33%;
  scroll-snap-align: center;
}

.inventory-list-wrap {
  margin-top: auto;
  margin-bottom: auto;
}

button.inventory-button {
  position: absolute;
  top: 50%;
  z-index: 10;
  width: 16px;
  height: 25px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: 0;
}

button.inventory-button::before,
button.inventory-button::after {
  content: '';
  position: absolute;
  left: 0;
  width: 16px;
  height: 3px;
  background-color: var(--bg-body);
}

button.inventory-button::before {
  bottom: 6px;
  transform: rotate(45deg);
}

button.inventory-button::after {
  top: 6px;
  transform: rotate(-45deg);
}

button.inventory-button--prev {
  left: 7px;
}

button.inventory-button--next {
  right: 7px;
  transform: scale(-1, 1);
}

.inventory-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.icon-stock::before {
  content: '';
  display: block;
  width: 56px;
  height: 56px;
  margin-bottom: 3px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.icon-stock--mat::before {
  background-image: url(../img/program/icon/mat.svg);
}

.icon-stock--chair::before {
  background-image: url(../img/program/icon/chair.svg);
}

.icon-stock--dumbbells::before {
  background-image: url(../img/program/icon/dumbbells.svg);
}

.program__description-content {
  display: flex;
}

.program__description-list-wrap,
.inventory-wrap {
  width: 100%;
  max-width: 520px;
  border-radius: 7px;
}

.schedule-list {
  display: flex;
  width: fit-content;
  margin: 0 auto;
  padding: 18px 0 40px;
  list-style-type: none;
}

.schedule-item {
  box-sizing: border-box;
  width: 106px;
  padding-right: 10px;
  padding-left: 10px;
  text-align: center;
}

.schedule-item dl,
.schedule-item dd {
  margin: 0;
}

.icon-schedule::before {
  content: '';
  display: block;
  width: 100%;
  height: 100px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.icon-schedule--extension::before {
  background-image: url(../img/program/extension.png);
  background-position: center top;
}

.icon-schedule--workout::before {
  background-image: url(../img/program/workout.png);
}

.icon-schedule--warmup::before {
  background-image: url(../img/program/warmup.png);
}

.details {
  display: block;
  box-sizing: border-box;
  width: 100%;
  border: 1px solid var(--bg-light);
  border-radius: 10px;
}

.details__summary {
  position: relative;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 6px 96px 6px 40px;
  font-size: 12px;
  color: var(--bg-body);
  background-color: var(--bg-dark);
  border-radius: 10px 10px 0 0;
  transition: border-radius var(--transition);
}

.details__day {
  margin-right: 40px;
}

.details--hidde .details__summary {
  border-radius: 10px;
}

.details__content {
  max-height: 100vh;
  opacity: 1;
  transition: max-height var(--transition), opacity var(--transition);
}

.details__content p {
  margin: 0;
}

.details--hidde .details__content {
  max-height: 0;
  opacity: 0;
}

.details__content--hidde {
  display: none;
}

.details__toggle {
  position: absolute;
  top: 50%;
  right: 21px;
  width: 18px;
  height: 18px;
  padding: 0;
  background-color: transparent;
  border: 0;
  transform: translateY(-50%);
  opacity: 0.4;
  transition: opacity var(--transition);
  appearance: none;
}

.details__toggle:hover {
  opacity: 1;
}

.details__toggle::before,
.details__toggle::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 18px;
  height: 3px;
  background-color: var(--bg-body);
  transform: translate(-50%, -50%);
}

.details__toggle::after {
  transition: width var(--transition), height var(--transition);
}

.details--hidde .details__toggle::after {
  width: 3px;
  height: 18px;
}

.details__done {
  display: flex;
  align-items: center;
  font-size: 20px;
  font-family: var(--font-bebas);
  opacity: 0.4;
  transition: opacity var(--transition);
}

.details__done:hover {
  opacity: 1;
}

.details__done-checkbox-view {
  position: relative;
  display: block;
  width: 36px;
  height: 36px;
  margin-right: 9px;
  border: 1px solid var(--bg-body);
  border-radius: 4px;
}

.details__done-checkbox-view::before {
  content: '';
  position: absolute;
  top: 4px;
  left: 12px;
  box-sizing: border-box;
  width: 12px;
  height: 20px;
  border: 3px solid transparent;
  border-top-width: 0;
  border-left-width: 0;
  transform: rotate(45deg);
  transition: border var(--transition);
}

.details__done-input:checked + .details__done-checkbox-view::before {
  border-color: var(--bg-body);
}

.page-footer {
  --color-footer-text: #85818c;
  font-size: 12px;
  line-height: 16px;
  color: var(--color-footer-text);
  background-color: var(--bg-dark);
  flex-grow: 0!important;
}

.page-footer a {
  color: inherit;
}

@media (min-width: 576px) {
